</template>

<script>

    import Header from '../common/Header.vue';
    
    export default {
        data() {
         return{
           title:'',
         }
        },
        components:{
           appHeader:Header
        },
        route:{
          activate:function(transition){
            document.title = this.title;
            this.$root.$set('header',this.title);
            transition.next();
          }
        },
    }
</script>

<style lang="sass">
 .register{
   .weui_cell{
    padding: 15px;
   }
   .weui_label{
     width: 5em;
   }
 }
</style>


