
// 
exports.numFormat = {
    write: function(val, oldVal) {
	    var number = Number(val);
	    if (isNaN(number)) {
	      return oldVal;
	    }else{
	      return (number <=0) ? 1 : number
	    }
	  }
    
}

/*
*jsonjson
*/
exports.jsonFormat = (val) => {
	try { 
	   return JSON.parse(val) 
	}catch (e) { 
	   return '';

	} 
}


exports.timeago = (times) => {

  let timestamp = Number(times);	
  let time = new Date(timestamp);

  let delta = parseInt((new Date() - time) / 1000, 10);
  if (delta <= 0) return 'just now';

  let minutes = delta / 60;
  // if (minutes < 1) return 'less than a minute ago';
  // if (minutes < 2) return 'about a minute ago';

  let hours = minutes / 60;
  if (hours < 1) return parseInt(minutes, 10) + ' ';
  // if (hours < 1.5) return 'about an hour ago';

  let days = hours / 24;
  if (days < 1) return Math.round(hours) + ' ';
  if (days < 7) return parseInt(days, 10) + ' ';

  let month = time.getMonth();
  if (month < 10) month = '0' + month;
  let date = time.getDate();
  if (date < 10) date = '0' + date;
  return [time.getFullYear(), month, date].join('-');

}

/**
 *   (M)(d)(h)(m)(s)(q)  1-2  
 *   (y) 1-4 (S) 1 ( 1-3 )
 *   
 *   "yyyy-MM-dd hh:mm:ss.S" ==> 2006-07-02 08:09:04.423
 *   "yyyy-M-d h:m:s.S"      ==> 2006-7-2 8:9:4.18 
 */
exports.dateFormat = (date, format) =>{
    date = Number(date);
    date = new Date(date);

    var map = {
        "M": date.getMonth() + 1, // 
        "d": date.getDate(), // 
        "h": date.getHours(), // 
        "m": date.getMinutes(), // 
        "s": date.getSeconds(), // 
        "q": Math.floor((date.getMonth() + 3) / 3), // 
        "S": date.getMilliseconds() // 
    };
    format = format.replace(/([yMdhmsqS])+/g, function(all, t){
        var v = map[t];
        if(v !== undefined){
            if(all.length > 1){
                v = '0' + v;
                v = v.substr(v.length-2);
            }
            return v;
        }
        else if(t === 'y'){
            return (date.getFullYear() + '').substr(4 - all.length);
        }
        return all;
    });
    return format;
};






